* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

.bg-img {
  background: url("http://localhost:8080/assets/landing-background.jpg") no-repeat center center fixed;
  background-size: cover;
  overflow: hidden;
  width: 100%;
  height: 100vh; }

.input {
  font-size: 24px; }

.grid {
  min-height: 100% !important;
  flex: 1; }

.marginSides {
  margin: 0 20px !important; }

.custom-form-card {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 30px;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  background-color: #32393d;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3); }

.warning {
  background-color: #cf8300 !important; }

.danger {
  background-color: #c03b3b !important; }

.info {
  background-color: #3b84c0 !important; }

.snackbarIcon {
  font-size: 20px;
  color: #fff; }

.imageUploadCard {
  margin: 10px;
  background-color: rgba(0, 0, 0, 0.1);
  width: 97%;
  padding: 5px;
  border-radius: 4px; }

.uploadIcon {
  color: white;
  font-size: 20px; }

.gamesGrid {
  overflow-x: auto;
  align-content: flex-start;
  width: initial;
  height: 350px; }
  .gamesGrid span {
    color: #bdc3c7;
    font-size: 16px;
    font-family: "Roboto";
    display: block;
    width: 100%;
    line-height: 1.5;
    height: min-content; }

.gameCheck {
  margin: 10px;
  cursor: pointer;
  position: relative;
  width: 100px;
  height: 100px;
  border-radius: 12px;
  transition: all 0.3s; }
  .gameCheck:hover {
    color: rgba(255, 255, 255, 0.3); }
  .gameCheck > div {
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    border-radius: 12px;
    transition: background-color 0.3s; }
    .gameCheck > div:hover {
      background-color: rgba(204, 210, 213, 0.25); }
    .gameCheck > div > div {
      margin: 4px;
      position: absolute;
      top: 0px;
      bottom: 0px;
      left: 0px;
      right: 0px;
      border-radius: 12px;
      border: #32393d solid 5px;
      transition: all 0.3s;
      display: flex;
      align-items: center;
      justify-content: center; }
      .gameCheck > div > div i {
        font-size: 3em;
        color: rgba(255, 255, 255, 0);
        transition: color 0.3s; }
  .gameCheck_selected > div {
    background-color: rgba(0, 0, 0, 0); }
    .gameCheck_selected > div > div {
      background-color: rgba(220, 115, 55, 0.65); }
      .gameCheck_selected > div > div i {
        color: white; }

.tooltip {
  font-size: 12px; }

.genreCheck {
  width: min-content;
  padding: 5px;
  margin: 5px; }
  .genreCheck div {
    background-color: rgba(30, 32, 36, 0.5);
    color: #ff9a60;
    display: flex;
    align-items: center;
    justify-content: center; }
    .genreCheck div i {
      font-size: 15px;
      display: flex;
      align-items: center;
      justify-content: center; }
  .genreCheck div.clicked {
    background-color: #1e2024; }

.scrollable {
  height: 350px;
  overflow-y: auto;
  flex-grow: 100; }
  .scrollable::-webkit-scrollbar-track {
    border-radius: 100px;
    background-color: #32393d; }
  .scrollable::-webkit-scrollbar {
    width: 7px;
    background-color: #32393d;
    border-radius: 100px; }
  .scrollable::-webkit-scrollbar-thumb {
    background-color: #1e2024;
    border-radius: 100px; }
